import { Component } from 'react';
import { withStyles } from 'vitaminjs';
import classnames from 'classnames';
import Prismic from 'prismic.io';

import s from './style.css';
import Map from './Map';
import Modale from './Modale';
import Details from './Details';
import Post from './Post';


const getSleepingLocations = () =>
    Prismic.api('http://vagalam.prismic.io/api')
        .then(api => api.query(Prismic.Predicates.at('document.type', 'sleep_location')))
        // .then(response => console.log(response.results));

class Trip extends Component {
    constructor(props) {
        super(props);
        this.handleKeyDown = this.handleKeyDown.bind(this);
        this.handleModalClose = this.handleModalClose.bind(this);
        this.state = { showPost: false }
    }
    componentDidMount() {
        getSleepingLocations();
    }
    handleKeyDown(e) {
        if (e.key === ' ') {
            this.setState(({ showPost }) => ({ showPost: !showPost }));
        }
    }
    handleModalClose() {
        this.setState({ showPost: false });
    }
    render() {
        return <div className={s.layout} onKeyDown={this.handleKeyDown} >
            <Modale isOpen={this.state.showPost} onClose={this.handleModalClose} >
                <Post
                    title="A new beginning"
                    date={new Date(2017, 6, 1)}
                    content={`
<p>Partout s'talait, se rpandait, s'baudissait le peuple en vacances. C'tait une de ces solennits sur lesquelles, pendant un long temps, comptent les saltimbanques, les faiseurs de tours, les montreurs d'animaux et les boutiquiers ambulants, pour compenser les mauvais temps de l'anne.</p>
<p>En ces jours-l il me semble que le peuple oublie tout, la douleur et le travail ; il devient pareil aux enfants. Pour les petits c'est un jour de cong, c'est l'horreur de l'cole renvoye  vingt-quatre heures. Pour les grands c'est un armistice conclu avec les puissances malfaisantes de la vie, un rpit dans la contention et la lutte universelles.</p>
<p>L'homme du monde lui-mme et l'homme occup de travaux spirituels chappent difficilement  l'influence de ce jubil populaire. Ils absorbent, sans le vouloir, leur part de cette atmosphre d'insouciance. Pour moi, je ne manque jamais, en vrai Parisien, de passer la revue de toutes les baraques qui se pavanent  ces poques solennelles.</p>
<p>Elles se faisaient en vrit, une concurrence formidable : elles piaillaient, beuglaient, hurlaient. C'tait un mlange de cris, de dtonations de cuivre et d'explosions de fuses. Les queues-rouges et les Jocrisses convulsaient les traits de leurs visages basans, racornis par le vent, la pluie et le soleil ; ils lanaient, avec l'aplomb des comdiens srs de leurs effets, des bons mots et des plaisanteries d'un comique solide et lourd comme celui de Molire. Les Hercules, fiers de l'normit de leurs membres, sans front et sans crne, comme les orangs-outangs, se prlassaient majestueusement sous les maillots lavs la veille pour la circonstance. Les danseuses, belles comme des fes ou des princesses, sautaient et cabriolaient sous le feu des lanternes qui remplissaient leurs jupes d'tincelles.</p>
<p>Tout n'tait que lumire, poussire, cris, joie, tumulte ; les uns dpensaient, les autres gagnaient, les uns et les autres galement joyeux. Les enfants se suspendaient aux jupons de leurs mres pour obtenir quelque bton de sucre, ou montaient sur les paules de leurs pres pour mieux voir un escamoteur blouissant comme un dieu. Et partout circulait, dominant tous les parfums, une odeur de friture qui tait comme l'encens de cette fte.</p>
<p>Au bout,  l'extrme bout de la range de baraques, comme si, honteux, il s'tait exil lui-mme de toutes ces splendeurs, je vis un pauvre saltimbanque, vot, caduc, dcrpit, une ruine d'homme, adoss contre un des poteaux de sa cahute ; une cahute plus misrable que celle du sauvage le plus abruti, et dont deux bouts de chandelles, coulants et fumants, clairaient trop bien encore la dtresse.</p>
<p>Partout la joie, le gain, la dbauche ; partout la certitude du pain pour les lendemains ; partout l'explosion frntique de la vitalit. Ici la misre absolue, la misre affuble, pour comble d'horreur, de haillons comiques, o la ncessit, bien plus que l'art, avait introduit le contraste. Il ne riait pas, le misrable ! Il ne pleurait pas, il ne dansait pas, il ne gesticulait pas, il ne criait pas ; il ne chantait aucune chanson, ni gaie, ni lamentable, il n'implorait pas. Il tait muet et immobile. Il avait renonc, il avait abdiqu. Sa destine tait faite.</p>
<p>Mais quel regard profond, inoubliable, il promenait sur la foule et les lumires, dont le flot mouvant s'arrtait  quelques pas de sa rpulsive misre ! Je sentis ma gorge serre par la main terrible de l'hystrie, et il me sembla que mes regards taient offusqus par ces larmes rebelles qui ne veulent pas tomber.</p>
<p>Que faire ? A quoi bon demander  l'infortun quelle curiosit, quelle merveille il avait  montrer dans ces tnbres puantes, derrire son rideau dchiquet ? En vrit, je n'osais; et dt la raison de ma timidit vous faire rire, j'avouerai que je craignais de l'humilier. Enfin, je venais de me rsoudre  dposer en passant quelque argent sur une de ses planches, esprant qu'il devinerait mon intention, quand un grand reflux de peuple, caus par je ne sais quel trouble, m'entrana loin de lui.</p>
<p>Et, m'en retournant, obsd par cette vision, je cherchai  analyser ma soudaine douleur, et je me dis : Je viens de voir l'image du vieil homme de lettres qui a survcu  la gnration dont il fut le brillant amuseur ; du vieux pote sans amis, sans famille, sans enfants, dgrad par sa misre et par l'ingratitude publique et dans la baraque de qui le monde oublieux ne veut plus entrer !</p>
`}
                    pictures={[
                        "https://vagalamapi.files.wordpress.com/2017/06/dscf3713.jpg?quality=80&strip=info&w=900",
                        "/assets/files/b2e24f6b7884a150397208cb09913252.jpg"
                    ]}
                />
            </Modale>
            <div className={classnames(s.map, { [s.zoom]: this.state.showPost }) } >
                <Map />
            </div>
            <Details />
        </div>
    } 
};

export default withStyles(s)(Trip);


